const IndexPage = ({data}) => {

  return(
    <Layout>
      {/* <Helmet abcd="HHH" /> */}
      <SEO 
        title="Bem Vindo!" 
        description="Elevamos o seu projeto ao próximo nível com uma melhor experiência digital. Desenvolvimento Web, Desenvolvimento Gráfico e muito mais. Encontre o seu caminho."
      />
        <HomeTitle title="find your path." leading="@pathfinderdesigns" />    
      
        {/* <VideoCaption video={captionVideo} caption="Elevamos o seu projeto ao próximo nível com uma melhor experiência digital." /> */}
        <ImageCaption image={data.bgGatsbyImage.childImageSharp.fluid} caption="Elevamos o seu projeto ao próximo nível com uma melhor experiência digital." />
      
        {/* <Introduction title="Uma Identidade" description="A sua marca, grande ou pequena, tem um super poder. Tudo o que ela faz tem o potencial de criar boas experiências e sensações na mente dos seus clientes. Todas as ações, por mais pequenas que sejam, têm impacto nessa perceção. Assim, é importante que tais ações estejam cobertas com uma boa identidade. A mesma aparência, a mesma tonalidade e o mesmo tom de voz. Uma boa identidade, pode tornar a sua marca, algo especial. Vamos criá-la juntos?" />   */}
      
        <Introduction title="O que fazemos?" description="Acreditamos que ter uma identidade forte e reconhecível é a chave para um negócio com sucesso. Assim, ajudamos pessoas e empresas a tomar decisões corretas, um passo de cada vez." />  

        <Portfolio />
    
        <Introduction title="O nosso trabalho" description="Visite-nos no behance ou no dribbble." button="true"/>
        <Container>
          <div style={{paddingBottom: '30px'}}>
            
          </div>
          
        </Container>
    </Layout>
  )
}

export default IndexPage

export const query = graphql`
    query {
        bgGatsbyImage: file(relativePath: {eq: "background-original.jpg"}) {
            childImageSharp {
              fluid(maxWidth: 4000, quality: 100) {
                
                ...GatsbyImageSharpFluid_withWebp
                ...GatsbyImageSharpFluidLimitPresentationSize
              }
            }
        }
    }
`

