import React from "react"

import {Container} from '../../styles/globalStyles'
import { ItemsFlex } from '../../styles/portfolioStyles'
import PortfolioItem from "./servicesItem"
import { StaticQuery, graphql } from "gatsby"
// import PropTypes from "prop-types"

const Portfolio = ({data}) => {

    return(
      <div>
            <Container >
                    <ItemsFlex >
                      {data.allServices.nodes.map(service => {
                          // console.log(service.localImage.childImageSharp.fluid)
                          return(
                            
                            <div key={service.serviceId}>
                                <PortfolioItem 
                                  reverse={service.serviceId%2!==0} 
                                  image={service.localImage.childImageSharp.fluid} 
                                  title={service.name} 
                                  description={service.description}/>
                            </div>
                        )
                      })}
                    </ItemsFlex>
                    <div style={{marginBottom: '100px'}}></div>
            </Container>
      </div>
    )
}

export default function MyHeader(props) {
  return (
    <StaticQuery
      query={graphql`
        
        query {
            allServices(sort: {fields: serviceId}) {
                nodes {
                    id
                    name
                    description
                    serviceId
                    localImage {
                      childImageSharp{
                        fluid(maxWidth: 500) {
                          ...GatsbyImageSharpFluid
                        }
                      }
                    }
                }
            }     
        }
      `}
      render={data => <Portfolio data={data} {...props} />}
    />
  )
}

